@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  width: 100%;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin: 0;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually_hidden:not(:active),
input[type="checkbox"].visually_hidden,
input[type="radio"].visually_hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  margin: 0;
  padding: 0;
  width: auto;
}
.page-header__wrapper {
  min-width: 320px;
  margin: 0 auto;
  background-color: #283645;
}
.page-header__logo-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-width: 100%;
  height: 70px;
  background-color: #283645;
}
.page-header__logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 65px;
  box-shadow: 0 1px 0 0 #495562, inset 0 -1px 0 0 #000000;
}
.page-header__logo-image {
  width: 75px;
  height: 24px;
  fill: #ffffff;
}
.page-header__logo:hover .page-header__logo-image {
  fill: #d22856;
}
.page-header__logo:active .page-header__logo-image {
  fill: #d22856;
  opacity: 0.3;
}
.page-header__logo-image-wrapper {
  width: 280px;
  height: 28px;
}
.page-header__logo-image-tablet {
  display: none;
}
.page-header__logo-image-desktop {
  display: none;
}
@media (min-width: 660px) {
  .page-header__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-width: 620px;
  }
  .page-header__logo-wrapper {
    height: 110px;
    box-shadow: 0 1px 0 0 #495562, inset 0 -1px 0 0 #000000;
  }
  .page-header__logo {
    min-width: 620px;
    box-shadow: none;
  }
  .page-header__logo-image-wrapper {
    width: 620px;
    height: 58px;
  }
  .page-header__logo:active .page-header__logo-image {
    fill: #ffffff;
    opacity: 0.3;
  }
  .page-header__logo-image-mobile {
    display: none;
  }
  .page-header__logo-image-tablet {
    display: block;
    width: 147px;
    height: 41px;
  }
}
@media (min-width: 960px) {
